import React, { useState } from "react";
import getYear from "date-fns/getYear";
import { DateObj, GetDatePropsOptions, Props, useDayzed } from "dayzed";

import IconButton from "../Button/IconButton";
import { colors, spacingMap } from "../styleguide";

import useCalendarOffset from "./hooks/useCalendarOffset";
import useDateInView from "./hooks/useDateInView";
import { DateButton } from "./DateButton";
import {
	defaultMonthNamesShort,
	defaultWeekdayNames,
} from "./DatePicker.consts";
import { getDefaultDates } from "./DatePicker.defaults";
import { StaticDatePickerProps } from "./DatePicker.types";
import MonthSelect from "./MonthSelect";
import {
	CalendarHeading,
	Calendars,
	CalendarWeekWrapper,
	DatePickerWrapper,
	MonthYearSelects,
	MonthYearWrapper,
	NoDateContainer,
	Weekday,
	WeekdayWrapper,
} from "./StaticDatePicker.styles";
import YearSelect from "./YearSelect";

const StaticDatePicker = ({
	dataQA,
	monthNames = defaultMonthNamesShort,
	weekdayNames = defaultWeekdayNames,
	dayzedProps: {
		minDate = getDefaultDates().defaultMinDate,
		maxDate = getDefaultDates().defaultMaxDate,
		date,
		...restOfDayzedProps
	},
	isRange = false,
	m,
	mb,
	ml,
	mr,
	mt,
	className,
}: StaticDatePickerProps) => {
	const { dateInView, setDateInView } = useDateInView(date);
	const { offset, setOffset } = useCalendarOffset(dateInView);
	const todaysDate = new Date();

	const useDayzedProps: Props = {
		minDate,
		maxDate,
		date: dateInView,
		offset,
		onOffsetChanged: (newOffset) => setOffset(newOffset),
		...restOfDayzedProps,
	};

	const { calendars, getBackProps, getForwardProps } = useDayzed(useDayzedProps);
	const [hoveredDate, setHoveredDate] = useState<Date | null>(null);

	const handleOnMouseEnter = (enteredDate: Date) => {
		setHoveredDate(enteredDate);
	};

	// this takes a number based on arrow keys hit. It will find the active element and move to a date button based on the number in the function
	const getKeyOffset = (number: number) => {
		const activeElement = document.activeElement;
		const buttons = document.querySelectorAll<HTMLButtonElement>("[data-calendar-button='calendar-date-button']");

		// we will update the tabindex so that if a user tabs from the focused button, focus will stay within the popover
		buttons.forEach((button, i) => {
			const newNodeKey = i + number;
			if (button == activeElement) {
				activeElement.setAttribute("tabindex", "-1");
				if (newNodeKey <= buttons.length - 1 && newNodeKey >= 0) {
					buttons[newNodeKey].focus();
					buttons[newNodeKey].tabIndex = 0;
				}
			}
		});
	};

	const handleKeyboard = (event: React.KeyboardEvent) => {
		switch (event.key) {
			case "ArrowLeft":
				getKeyOffset(-1);
				break;
			case "ArrowRight":
				getKeyOffset(1);
				break;
			case "ArrowUp":
				getKeyOffset(-7);
				break;
			case "ArrowDown":
				getKeyOffset(7);
				break;
		}
	};

	const getTabIndex = (dateObj: DateObj) => {
		let tabIndex = dateObj.today ? 0 : -1;
		// this is required because selected can be either a Date or an array of Dates
		const selectedDateArray = restOfDayzedProps.selected ? restOfDayzedProps.selected.toString().split(",") : [""];

		// if we have a date selected, we want to tab to that date instead of today's date
		if (selectedDateArray.length > 0 && selectedDateArray[0] !== "") {
			tabIndex = dateObj.selected ? 0 : -1;
		}

		// check if selected date or today's date are in the calendar in view
		let selectedShown = false;
		let todayShown = false;
		calendars.forEach((cal) => {
			let calMonth = cal.month;
			let calYear = cal.year;
			selectedDateArray.filter((selectDate) => {
				if (!selectedShown) {
					const selectedDateObj = new Date(selectDate);
					selectedShown = calMonth === selectedDateObj.getMonth() && calYear == selectedDateObj.getFullYear();
				}
			});
			if (!todayShown) {
				todayShown = calMonth == todaysDate?.getMonth() && calYear == todaysDate?.getFullYear();
			}
		});

		// if selected date and today's date are not in view, tab to the first day of the month
		if (!(selectedShown || todayShown) && dateObj.date.getDate() === calendars[0].firstDayOfMonth.getDate()) {
			tabIndex = 0;
		}

		// if there is a selected date (or range of dates)
		// and the (1st) selected date is not in view,
		// and if today's date is in view, tab to that date
		if (selectedDateArray.length > 0 && selectedDateArray[0] !== ""
			&& !selectedShown && todayShown && dateObj.today) {
			tabIndex = 0;
		}

		return tabIndex;
	};

	const getDay = (dateObj: "" | DateObj, index: number) => {
		if (!dateObj) {
			return (
				<NoDateContainer key={`no-date-${index}`} />
			);
		}

		let dateProps: GetDatePropsOptions = {
			dateObj,
		};

		if (isRange) {
			dateProps = {
				...dateProps,
				onMouseEnter: () => handleOnMouseEnter(dateObj.date),
				onFocus: () => handleOnMouseEnter(dateObj.date),
			};
		}

		return (
			<DateButton
				key={dateObj.date.getTime()}
				dayzedProps={useDayzedProps}
				hoveredDate={hoveredDate}
				dateObj={dateObj}
				dateProps={dateProps}
				isRange={isRange}
				tabIndex={getTabIndex(dateObj)}
			/>
		);
	};

	const datePickerWrapperRangeProps = {
		onMouseLeave: () => setHoveredDate(null),
		onBlur: () => setHoveredDate(null),
	};

	// helps to prevent HTML bloat since the MonthYearWrapper is only needed when there are multiple months displayed
	const MonthYearsContainer = useDayzedProps.monthsToDisplay && useDayzedProps.monthsToDisplay > 1 ? (
		MonthYearWrapper
	) : (
		React.Fragment
	);

	return (
		<DatePickerWrapper
			data-qa={dataQA}
			m={m}
			mb={mb}
			mt={mt}
			mr={mr}
			ml={ml}
			className={className}
			{...(isRange && datePickerWrapperRangeProps)}
		>
			<CalendarHeading>
				<IconButton
					variant="tertiary"
					dataQA="calendar-back"
					icon="ChevronLeft"
					mr={spacingMap.xxs}
					{...getBackProps({ calendars })}
				/>
				<MonthYearsContainer>
					{calendars.map((calendar, calIndex) =>
						<MonthYearSelects key={`${calendar.month}-${calendar.year}-${dataQA}`} data-qa="month-year">
							<MonthSelect
								dataQA={dataQA}
								month={calendar.month}
								calendarIndex={calIndex}
								monthNames={monthNames}
								setDate={setDateInView}
								calendarOffset={offset}
								setCalendarOffset={setOffset}
								currentSelectedYear={calendar.year}
								minDate={minDate}
								maxDate={maxDate}
							/>
							<YearSelect
								dataQA={dataQA}
								currentSelectedYear={calendar.year}
								month={calendar.month}
								minYear={getYear(minDate)}
								maxYear={getYear(maxDate)}
								setDate={setDateInView}
								calendarOffset={offset}
								setCalendarOffset={setOffset}
								calendarIndex={calIndex}
							/>
						</MonthYearSelects>,
					)}
				</MonthYearsContainer>
				<IconButton
					variant="tertiary"
					dataQA="calendar-forward"
					icon="ChevronRight"
					ml={spacingMap.xxs}
					{...getForwardProps({ calendars })}
				/>
			</CalendarHeading>
			<Calendars
				onKeyDown={handleKeyboard}
			>
				{calendars.map((calendar) => (
					<div key={`${calendar.month}${calendar.year}`}>
						<WeekdayWrapper>
							{weekdayNames.map((weekday) => (
								<Weekday
									dataQA={`${weekday.label}-weekday`}
									variant="caption"
									color={`${colors.black}A8`}
									key={`${calendar.month}${calendar.year}${weekday.value}`}
									aria-label={weekday.label}
								>
									{weekday.value}
								</Weekday>
							))}
						</WeekdayWrapper>
						{
							calendar.weeks.map((week, weekIndex) =>
								<CalendarWeekWrapper key={weekIndex}>
									{week.map((dateObj, index) => getDay(dateObj, index))}
								</CalendarWeekWrapper>,
							)
						}
					</div>
				))}
			</Calendars>
		</DatePickerWrapper >
	);
};

export default StaticDatePicker;
